___CSS_LOADER_EXPORT___.push([module.id, `.add-task-div {
  margin: auto;

  .add-task-button {
    border: none;
    padding: 1rem 1.8rem;
    background: #ffffff;
    margin: auto;
    margin-top: 2rem;
    /* border: 2px solid rgb(255, 5, 5); */
    border-radius: 5%;
    /* font-size: x-large; */
  }

  .add-task-button:hover,
  .add-task-button:focus {
    background: #ffffff;
  }

  .add-task-button:active {
    transform: scale(0.9);
    transition: 0.1s;
  }
}

.add-task-modal {
  border-radius: 8px;
  background: white;
  margin: auto;
  /* transform: scale(1); */
  border: none;

  .modal-title {
    width: 100%;
    background: #000;
    color: white;
    text-align: center;
    padding: 1rem;
    border: 2px solid white;
    border-radius: inherit;
  }

  form div {
    padding: 0.8em 1em;
    display: block;
    margin: auto;
  }

  .form-controls {
    input,
    textarea {
      all: unset;
      appearance: none;
      display: block;
      width: 100%;
      padding: 0.3em 0.5em;
      background: rgb(240, 237, 237);
      border-radius: 4px;
      box-sizing: inherit;
      /* border: none; */
    }

    textarea:active,
    textarea:focus {
      background: var(--gradientBlackBg);
      color: white;
    }

    input[type="number"] {
      width: inherit;
      display: inline;
    }

    label[for="new-task-priority"] {
      display: block;
    }

    .priority-hint {
      color: rgba(0, 0, 0, 0.849);
      font-size: 1.2rem;
      margin: 0;
      padding: 0;
    }
  }

  .form-controls input:active,
  .form-controls input:focus {
    background: var(--gradientBlackBg);
    color: white;
  }

  .form-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border: 1px solid black; */

    button {
      appearance: none;
      all: unset;
      border-radius: 4px;
      padding: 0.8em 1.5em;
      border: 0.5px solid black;
      color: white;
      font-weight: 700;
      transition: 0.2s;
    }

    button:active,
    button:focus {
      transform: scale(0.9);
    }

    #cancel-button {
      background: #e20505;
    }

    #confirm-button {
      background: #2196f3;
    }

    #cancel-button:hover,
    #cancel-button:active,
    #cancel-button:focus {
      cursor: pointer;
      background: #ca0707;
    }

    #confirm-button:hover,
    #confirm-button:active,
    #confirm-button:focus {
      cursor: pointer;
      background: #157ed4;
    }
  }
}

dialog::backdrop {
  background: #2c2c2c;
  opacity: 0.7;
}

dialog[open] {
  animation: popup 0.2s ease-in forwards;
}

dialog[closed] {
  animation: popdown 0.2s ease-in forwards;
}

@keyframes popup {
  0% {
    transform: scale(0);
  }

  25% {
    transform: scale(0.25);
  }

  50% {
    transform: scale(0.5);
  }
  75% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes popdown {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(0.5);
  }

  50% {
    transform: scale(0.5);
  }
  75% {
    transform: scale(0.25);
  }
  100% {
    transform: scale(0);
  }
}
